<template>
  <div>
    <div class="vld-parent">
    </div>

    <table class="table mt-5">
      <thead>
        <tr>
          <!--  -->
          <th width="120"></th>
          <th width="150">Email</th>
          <th></th>
          <th width="100"></th>
          <th width="100"></th>
          <th width="80"></th>
        </tr>
      </thead>
      <tbody>
        <!-- item.id  key  item.id -->
        <tr v-for="(item) in orders" :key="item.id">
          <td>{{ item.create_at | time}}</td>
          <td>{{ item.user.email }}</td>
          <td>
            <ul class="list-unstyled">
              <li v-for="(product, index) in item.products" :key="index">
                {{ product.product.title }} {{ product.qty }}
                {{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td>
            <!--  is_enabled == 1  -->
            <span v-if="item.is_paid" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-main btn-sm" @click="openModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <!--  pagination  Pagination.vue  props  pagination-data  -->
    <Pagination :pagination-data="pagination" @changepage="getOrders"></Pagination>

    <!-- Modal -->
    <div
      class="modal fade"
      id="orderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <div class="form-check">
                <!--  API  0  1  true/false  0/1 -->
                <input
                  class="form-check-input"
                  type="checkbox"
                  id="is_paid"
                  v-model="tempOrder.is_paid"
                />
                <label class="form-check-label" for="is_paid"></label>
                {{tempOrder.id}}
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <!--  -->
            <button type="button" class="btn btn-main" @click="updateOrder"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import jquery  jquery
// $ import jquey from 'jquery'; 
import $ from 'jquery';
// 
import Pagination from '../../components/Pagination.vue';
//  export 
export default {
  components: {
    // 
    Pagination,
  },
  data() {
    return {
      orders: [],
      // 
      pagination: {},
      //  Modal 
      tempOrder: {},
    };
  },
  methods: {
    //  page  1
    getOrders(page = 1) {
      //  admin  API
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`;
      const vm = this;
      //  true
      vm.$store.state.isLoading = true;
      vm.$http.get(api).then((response) => {
        //  false
        vm.$store.state.isLoading = false;
        // 
        vm.orders = response.data.orders;
        // 
        vm.pagination = response.data.pagination;
      });
    },
    //  Modal 
    openModal(item) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/order/${item.id}`;
      const vm = this;
      vm.$http.get(api).then(() => {
        //  Object.assign  item 
        vm.tempOrder = Object.assign({}, item);
        $('#orderModal').modal('show');
      });
    },
    // 
    updateOrder() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
      vm.$http
        .put(api, {
          data: vm.tempOrder,
        })
        .then((response) => {
          //  modal 
          if (response.data.success) {
            $('#orderModal').modal('hide');
            vm.getOrders();
          } else {
            $('#orderModal').modal('hide');
          }
        });
    },
  },
  //  created 
  created() {
    this.getOrders();
  },
};
</script>
