import axios from 'axios';
import $ from 'jquery';

export default {
  namespaced: true,
  state: {
    // 
    cartItem: [],
    // 
    status: {
      loadingItem: '',
    },
  },
  actions: {
    // 
    getCart(context, open) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      if (open === 1) {
        $('.cart-dropdown').toggleClass('show');
      }
      axios.get(api).then((response) => {
        context.commit('CARTITEM', response.data.data);
      });
    },
    // 
    addtoCart(context, { id, qty }) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      context.commit('LOADING', true, { root: true });
      //  ID  loadingItem
      context.commit('LOADINGITEM', id);
      const cart = {
        product_id: id,
        //  qty 
        qty,
      };
      // {data: }
      axios
        .post(api, {
          data: cart,
        })
        .then(() => {
          // Modal  loadingItem 
          context.commit('LOADINGITEM', '');
          // 
          context.commit('LOADING', false, { root: true });
          context.dispatch('getCart');
        });
    },
    // 
    removeCartItem(context, id) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      context.commit('LOADINGITEM', id);
      context.commit('LOADING', true, { root: true });
      axios.delete(api).then(() => {
        context.commit('LOADINGITEM', '');
        context.commit('LOADING', false, { root: true });
        // 
        context.dispatch('getCart');
      });
    },
    //  dropmenu 
    toggleCart() {
      $(document).on('click', (e) => {
        //  .cart-dropdown  .cart-dropdown  .show 
        //  .cart-dropdown  .show 
        if (
          !e.target.closest('.cart-dropdown')
              && $('.cart-dropdown').hasClass('show')
        ) {
          $('.cart-dropdown').removeClass('show');
        }
      });
    },
  },
  mutations: {
    CARTITEM(state, payload) {
      state.cartItem = payload;
    },
    LOADINGITEM(state, payload) {
      state.status.loadingItem = payload;
    },
  },
  getters: {
    status: state => state.status,
    cartItem: state => state.cartItem,
    cartQuantity: (state) => {
      if (!state.cartItem.carts) {
        return 0;
      }
      return state.cartItem.carts.length;
    },
  },
};
