<template>
  <!--  -->
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <!--  pagination.has_pre  false () -->
      <li class="page-item" :class="{'disabled': !paginationData.has_pre}">
        <!--  pagination.current_page -1  getProducts  -->
        <a
          class="page-link"
          href="#"
          aria-label="Previous"
          @click.prevent="changePage(paginationData.current_page - 1)"
        >
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <!-- pagination.current_page == page  active  -->
      <li
        class="page-item"
        v-for="page in paginationData.total_pages"
        :key="page"
        :class="{'active': paginationData.current_page == page}"
      >
        <!--  getProducts  -->
        <a class="page-link" href="#" @click.prevent="changePage(page)">{{ page }}</a>
      </li>
      <!--  pagination.has_next  false () -->
      <li class="page-item" :class="{'disabled': !paginationData.has_next}">
        <!--  pagination.current_page +1  getProducts  -->
        <a
          class="page-link"
          href="#"
          aria-label="Next"
          @click.prevent="changePage(paginationData.current_page + 1)"
        >
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    // 
    paginationData: {
      // 
      type: Object,
      //  eslint 
      default: () => {},
    },
  },
  methods: {
    changePage(page) {
      this.$emit('changepage', page);
    },
  },
};
</script>
