<template>
  <div>
    <div class="text-right mt-4">
      <!--  true  -->
      <button class="btn btn-main" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th width="80"></th>
          <th width="120"></th>
          <th width="80"></th>
          <th width="80"></th>
          <th width="150" class="text-center"></th>
        </tr>
      </thead>
      <tbody>
        <!-- item.id  key  item.id -->
        <tr v-for="(item) in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.percent }} %</td>
          <td>{{ item.due_date | time }}</td>
          <td class="text-right">{{ item.code }}</td>
          <td>
            <!--  is_enabled == 1  -->
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <!--  false item  -->
            <button class="btn btn-outline-main btn-sm" @click="openModal(false, item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="deleteModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <!--  pagination  Pagination.vue  props  pagination-data  -->
    <Pagination :pagination-data="pagination" @changepage="getCoupon"></Pagination>

    <!-- Modal -->
    <!-- / Coupon -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input
                type="text"
                class="form-control"
                id="title"
                v-model="tempCoupon.title"
                placeholder=""
              />
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="percent_off"></label>
                <input
                  type="number"
                  class="form-control"
                  id="percent_off"
                  v-model="tempCoupon.percent"
                  placeholder=""
                />
              </div>
              <div class="form-group col-md-6">
                <label for="code">Code</label>
                <input
                  type="text"
                  class="form-control"
                  id="code"
                  v-model="tempCoupon.code"
                  placeholder=" Code"
                />
              </div>
            </div>
            <hr />
            <div class="form-group">
              <label for="last_date"></label>
              <input type="date" class="form-control" id="last_date" v-model="last_date" />
            </div>
            <div class="form-group">
              <div class="form-check">
                <!--  API  0  1  true/false  0/1 -->
                <input
                  class="form-check-input"
                  type="checkbox"
                  id="is_enabled"
                  v-model="tempCoupon.is_enabled"
                  :true-value="1"
                  :false-value="0"
                />
                <label class="form-check-label" for="is_enabled"></label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <!--  -->
            <button type="button" class="btn btn-main" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>

    <!--  Coupon -->
    <div
      class="modal fade"
      id="delcouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import jquery  jquery
// $ import jquey from 'jquery'; 
import $ from 'jquery';
// 
import Pagination from '../../components/Pagination.vue';
//  export 
export default {
  components: {
    // 
    Pagination,
  },
  data() {
    return {
      coupons: [],
      // 
      pagination: {},
      //  Modal 
      tempCoupon: {},
      couponLastday: '',
      isNew: false,
      // 
      last_date: new Date(),
    };
  },
  // 
  //  timestamp 
  watch: {
    last_date() {
      const vm = this;
      const timestamp = Math.floor(new Date(vm.last_date) / 1000);
      vm.tempCoupon.due_date = timestamp;
    },
  },
  methods: {
    //  page  1
    getCoupon(page = 1) {
      //  admin  API
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      //  true
      vm.$store.state.isLoading = true;
      vm.$http.get(api).then((response) => {
        //  false
        vm.$store.state.isLoading = false;
        // 
        vm.coupons = response.data.coupons;
        // 
        vm.pagination = response.data.pagination;
      });
    },
    //  Modal 
    openModal(isNew, item) {
      const vm = this;
      if (isNew) {
        vm.tempCoupon = {};
        vm.isNew = true;
      } else {
        //  Object.assign  item 
        vm.tempCoupon = Object.assign({}, item);
        // 
        // IOS YYYY-MM-DDTHH:mm:ss.sssZ
        const couponDate = new Date(vm.tempCoupon.due_date * 1000)
          .toISOString()
          .split('T');
        //  couponDate 
        // 0 - 
        // 1 - 
        const today = couponDate[0];
        vm.last_date = today;
        vm.isNew = false;
      }
      $('#couponModal').modal('show');
    },
    //  Modal
    deleteModal(item) {
      this.tempCoupon = Object.assign({}, item);
      $('#delcouponModal').modal('show');
    },
    //  post 
    updateCoupon() {
      //  API const  let
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
      //  post
      let httpMethod = 'post';
      const vm = this;
      // 
      if (!vm.isNew) {
        //  API
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        //  API  put put
        httpMethod = 'put';
      }
      //  API  { data:{} } 
      vm.$http[httpMethod](api, {
        data: vm.tempCoupon,
      }).then((response) => {
        //  modal 
        if (response.data.success) {
          $('#couponModal').modal('hide');
          vm.getCoupon();
        } else {
          $('#couponModal').modal('hide');
        }
      });
    },
    deleteCoupon() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.$http.delete(api).then((response) => {
        //  modal 
        if (response.data.success) {
          $('#delcouponModal').modal('hide');
          vm.getCoupon();
        } else {
          $('#delcouponModal').modal('hide');
        }
      });
    },
  },
  //  created 
  created() {
    this.getCoupon();
  },
};
</script>
