<template>
  <div>
    <div class="text-right mt-4">
      <!--  true  -->
      <button class="btn btn-main" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <!--  -->
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="80"></th>
          <th width="150" class="text-center"></th>
        </tr>
      </thead>
      <tbody>
        <!-- item.id  key  item.id -->
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.origin_price | currency }}</td>
          <td class="text-right">{{ item.price | currency }}</td>
          <td>
            <!--  is_enabled == 1  -->
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <!--  false item  -->
            <button class="btn btn-outline-main btn-sm" @click="openModal(false, item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="deleteModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <!--  pagination  Pagination.vue  props  pagination-data  -->
    <Pagination :pagination-data="pagination" @changepage="getProducts"></Pagination>

    <!-- Modal -->
    <!-- / -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <!--  -->
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <!--  API  0  1  true/false  0/1 -->
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <!--  -->
            <button type="button" class="btn btn-main" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  Modal -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import jquery  jquery
// $ import jquey from 'jquery'; 
import $ from 'jquery';
// 
import Pagination from '../../components/Pagination.vue';
//  export 
export default {
  components: {
    // 
    Pagination,
  },
  data() {
    return {
      products: [],
      // 
      pagination: {},
      //  Modal 
      tempProduct: {},
      isNew: false,
      status: {
        fileUploading: false,
      },
    };
  },
  methods: {
    //  page  1
    getProducts(page = 1) {
      //  admin  API
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`;
      const vm = this;
      //  true
      vm.$store.state.isLoading = true;
      vm.$http.get(api).then((response) => {
        //  false
        vm.$store.state.isLoading = false;
        // 
        vm.products = response.data.products;
        // 
        vm.pagination = response.data.pagination;
      });
    },
    //  Modal 
    openModal(isNew, item) {
      const vm = this;
      if (isNew) {
        vm.tempProduct = {};
        vm.isNew = true;
      } else {
        //  Object.assign  item 
        vm.tempProduct = Object.assign({}, item);
        vm.isNew = false;
      }
      $('#productModal').modal('show');
    },
    //  Modal
    deleteModal(item) {
      this.tempProduct = Object.assign({}, item);
      $('#delProductModal').modal('show');
    },
    //  post 
    updateProduct() {
      //  API const  let
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      //  post
      let httpMethod = 'post';
      const vm = this;
      // 
      if (!vm.isNew) {
        //  API
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        //  API  put put
        httpMethod = 'put';
      }
      //  API  { data:{} } 
      vm.$http[httpMethod](api, {
        data: vm.tempProduct,
      }).then((response) => {
        //  modal 
        if (response.data.success) {
          $('#productModal').modal('hide');
          vm.getProducts();
        } else {
          $('#productModal').modal('hide');
        }
      });
    },
    deleteProduct() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      vm.$http.delete(api).then((response) => {
        //  modal 
        if (response.data.success) {
          $('#delProductModal').modal('hide');
          vm.getProducts();
        } else {
          $('#delProductModal').modal('hide');
        }
      });
    },
    uploadFile() {
      // 
      const uploadedFile = this.$refs.files.files[0];
      // 
      const resetFile = this.$refs.files.id;
      const vm = this;
      // formData ( formData )
      const formData = new FormData();
      // 
      vm.status.fileUploading = true;
      // file-to-upload  API uploadedFile 
      //  appdend  formData
      formData.append('file-to-upload', uploadedFile);
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
      //  post formData(API  enctype="multipart/form-data")
      vm.$http
        .post(api, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then((response) => {
          // 
          vm.status.fileUploading = false;
          // 
          if (response.data.success) {
            //  data  imageUrl set 
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
            // 
            document.getElementById(resetFile).value = '';
          } else {
            vm.$bus.$emit(
              'message:push',
              response.data.message.message,
              'danger',
            );
          }
        });
    },
  },
  //  created 
  created() {
    this.getProducts();
  },
};
</script>
